@use 'hero';
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box  ;
    font-family: Roboto, sans-serif;
}
.container{
    max-width: 960px;
    width: 100%;
    margin: 0 auto;
  
    @media (max-width:640px) {
        max-width: 80%;
    }
    @media (max-width: 1024px){
        max-width: 90%;
    }
}